/* start page title and header */
.header {
  h1 {
    border-radius: 1.5rem;
  }
}

.page-header {
  text-align: center;
  h2 {
    background-color: #ee9e29;
    color: #000;
    display: inline-block;
    padding: 0.75rem 5rem;
    border-radius: 1rem;
    border: 1px solid #b58d5a;
    a {
      color: #000;
    }
  }
}
/* end page title and header */