// start navbar
nav.custom-navigation-bar {
  position: fixed;
  bottom: 0;
  background: #fff;
  width: 100%;
  box-shadow: 0 -1px 1rem rgb(0 0 0 / 20%);
  padding: 1rem 0;
  ul {
    list-style-type: none;
    display: flex;
    margin: 0 auto;
    width: 100%;
    padding: 0;
    justify-content: space-evenly;
    a {
      font-weight: bold;
    }
  }
}

@media (max-width: 768px) {
  nav.custom-navigation-bar {
    ul {
      justify-content: space-between;
      li {
        width: 25%;
      }
      a {
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        i {
          width: 30px;
          height: 30px;
          line-height: 30px;
          font-size: 20px;
          text-align: center;
        }
        span {
          display: block;
          font-size: 0.7rem;
        }
      }
    }
  }
}
// end navbar