/* start admin page */
.row-title {
    height: 38px;
    border-radius: 0.25rem;
    text-align: center;
    color: #fff;
    font-size: 1.3rem;
    line-height: 38px;
}

@media (max-width: 768px) {
    .row-card {
        margin-bottom: 2rem;
        border-bottom: 1px solid #ddd;
        padding-bottom: 1rem;
        button,
        input {
            margin-top: 0.5rem;
        }
    }
}
/* end admin page */